.movie_detail{
  margin: 70px 0;
  width: calc(100vw - 400px);
  height: calc( 100% - 140px);
  background: #fff;
  position: relative;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
      0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    border-radius: 5px;
    position: relative;
}
.movie_detail .imgBox{
  max-width: 35%;
  height: 100%;
  border-radius: 5px 0 0 5px;
  position: absolute;
  left: 0;
  overflow: hidden;
}
.movie_detail .movie_poster{
  height: 100%;
}
.movie_detail .movie_data{
  position: absolute;
  left: 40%;
  width: 55%;
  display: inline-block;
  font-size: 16px;
}
.movie_detail .movie_title{
  font-size: 32px;
  margin: 32px 0 15px;
}
.movie_detail .movie_year{
  font-size: 16px;
  margin: 15px 0;
  color: #888;
}
.movie_detail .movie_genres{
  list-style: none;
  color:#bd5b63;
  font-weight:500;
  margin: 5px 0;
  padding:0;
  display: inline-block;
}
.movie_detail .movie_genres li{
  display: inline-block;
  margin-right: 10px;
  font-size: 18px;
}
.movie_detail .movie_summary{
  font-size: 16px;
  line-height: 1.4;
}
.movie_detail .movie_summary::before{
  content: "";
  display: block;
  height: 2px;
  width: 30px;
  background-color: #bd5b63;
  margin-bottom:20px;
}