*{
    box-sizing: border-box;
}



body{
    margin: 0;
    padding:0;
    background-color: #eff3f7;
    font-family:'Roboto', 'Open Sans', sans-serif;   
    height: 100%;
}
html, body, #root ,.container{
    height: 100%;
    display: flex;
    justify-content: center;
}
.loader{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 300;
}
.movies{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding:50px;
    padding-top: 70px;
    width: 80%;
}

.loader_text{
    font-size: 30px;
    animation: ani 1.2s ease-in infinite  ;
    font-weight: 700;
    color:#bd5b63;
}

@keyframes ani {
    0%{
        opacity: 0%;
    }
    50%{
        opacity: 100%;
    }
    100%{
        opacity: 0%;
    }
}
