.movies .movie{
    width: 45%;
    background-color: #fff;
    margin-bottom: 70px;
    font-weight: 300;
    padding:20px;
    color:#adaeb9;
    border-radius: 5px;
    box-shadow:  0 13px 27px -5px rgba(50,50,93,0.25),
    0 8px 16px -8px rgba(0,0,0,0.3), 0 -6px 16px -6px rgba(0,0,0,0.025);
}
.movies .movie a {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    text-decoration: none;
    color: inherit;
  }
.movie img{
    position: relative;
    top:-50px;
    max-width: 180px;
    width: 100%;
    margin-right: 20px;
    border-radius: 5px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}
.movie .movie_data{
    width: calc(100% - 180px);
}

.movie .movie_title{
    margin: 0;
    font-weight: 700;
    margin-bottom: 10px;
    font-size: 20px;
}
.movie .movie_year{
    margin: 0;
    font-weight: 300;
}
.movie h3{
    margin-bottom: 5px;
    font-size: 24px;
    color:#2c2c2c
}
.movie .movie_genres{
    list-style: none;
    color:#bd5b63;
    font-weight:500;
    margin: 5px 0;
    padding:0;
}
.movie .movie_genres li{
    display: inline-block;
    margin-right: 10px;
    font-size: 14px;
}
.movie .movie_summary{
    font-size: 14px;
    margin-bottom: 0;
}